import React, {forwardRef } from 'react';
import { styled } from '@mui/system';

const SectionContainer = styled('section')({
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  flexDirection: 'column',
  textAlign: 'center',
});

const About = forwardRef((props, ref) => {
  return (
    <SectionContainer id="about" ref={ref}>
  <p style={{ fontSize: "1.2rem", lineHeight: "1.8", textAlign: "justify", marginBottom: "1.5rem", color: "#999" }}>
    Bem-vindo  <span style={{ color: "#F1C40F", fontWeight: "bold" }}>Cazarotto Joalheria</span>, onde voc encontrar joias deslumbrantes, relgios de alta qualidade, culos estilosos e presentes exclusivos. Nossa misso  proporcionar uma experincia nica de elegncia e sofisticao, ajudando voc a celebrar momentos inesquecveis.
  </p>
  <p style={{ fontSize: "1.2rem", lineHeight: "1.8", textAlign: "justify", marginBottom: "1.5rem", color: "#999" }}>
    Descubra nossa coleo cuidadosamente selecionada:
  </p>
  <ul style={{ fontSize: "1.2rem", lineHeight: "1.8", color: "#999", marginBottom: "1.5rem", marginLeft: "30px", listStyle: "none" }}>
  <li style={{ position: "relative", paddingLeft: "20px" }}>
    <span style={{ color: "#F1C40F", position: "absolute", left: "0" }}></span>
    Joias delicadas e sofisticadas para qualquer ocasio
  </li>
  <li style={{ position: "relative", paddingLeft: "20px" }}>
    <span style={{ color: "#F1C40F", position: "absolute", left: "0" }}></span>
    Relgios de alta qualidade e design excepcional
  </li>
  <li style={{ position: "relative", paddingLeft: "20px" }}>
    <span style={{ color: "#F1C40F", position: "absolute", left: "0" }}></span>
    culos de marca com estilo nico
  </li>
  <li style={{ position: "relative", paddingLeft: "20px" }}>
    <span style={{ color: "#F1C40F", position: "absolute", left: "0" }}></span>
    Presentes personalizados para momentos especiais
  </li>
</ul>

  <p style={{ fontSize: "1.2rem", lineHeight: "1.8", textAlign: "justify", color: "#999", marginBottom: "1.5rem" }}>
    Conhea nossos produtos e experimente o brilho nico da <span style={{ color: "#F1C40F", fontWeight: "bold" }}>Cazarotto Joalheria</span>
  </p>
  <p style={{ fontSize: "1.2rem", lineHeight: "1.8", textAlign: "justify", color: "#999" }}>
    Estamos aqui para ajud-lo a encontrar o presente perfeito ou aquele detalhe especial que voc merece.
  </p>
</SectionContainer>

  );
});

export default About;
